import React from 'react'
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  Button,
  useDisclosure,
  FormControl,
  Input,
  useToast,
  Box,
} from "@chakra-ui/react";
import axios from "axios";
import { useState } from "react";
import { ChatState } from "../../Context/ChatProvider";
import UserBadgeItem from "../UserComponents/UserBadgeItem";
import UserListItem from "../UserComponents/UserListItem ";

const GroupChatModal = ({children}) => {
        const { isOpen, onOpen, onClose } = useDisclosure();
        const [groupChatName, setGroupChatName] = useState();
        const [selectedUsers, setSelectedUsers] = useState([]);
        const [search, setSearch] = useState("");
        const [searchResult, setSearchResult] = useState([]);
        const { user, chats, setChats } = ChatState();
        const [loading, setLoading] = useState(false);
        const toast = useToast();
        
        const handleGroup = (userToAdd) => {
            if (selectedUsers.includes(userToAdd)) {
                 toast({
                    title: "User already added",
                    status: "warning",
                    duration: 5000,
                    isClosable: true,
                    position: "top",
                });   
                return ;            
            }
            setSelectedUsers([...selectedUsers,userToAdd]);
        };

        const handleSearch = async(query) => {
             setLoading(true);
             setSearch(query);
                if(!query){
                    return;
                }
                try {
                    setLoading(true);
                    const config = {
                        headers : {
                             Authorization: `Bearer ${user.token}`,
                        },
                    };
                    const {data} = await axios.get(`/api/v1/user/searchUsers?search=${search}`, config);
                    console.log(data);
                    setLoading(false);
                    setSearchResult(data);
                } catch (error) {
                      toast({
                        title: "Error Occured!",
                        description: "Failed to Load the Search Results",
                        status: "error",
                        duration: 5000,
                        isClosable: true,
                        position: "bottom-left",
                    });
                }
         }

         const handleDelete = (delUser) => {
            setSelectedUsers(selectedUsers.filter((sel)=> sel._id !== delUser._id));
         }
         const handleSubmit = async() => {
            if(!groupChatName || !selectedUsers){
                  toast({
                    title: "Please fill all the feilds",
                    status: "warning",
                    duration: 5000,
                    isClosable: true,
                    position: "top",
                });
                return;
            }
            try {
                 const config = {
                        headers: {
                        Authorization: `Bearer ${user.token}`,
                        },
                    };
                const {data} = await axios.post(`api/v1/chat/group`,{
                    name : groupChatName,
                    users :JSON.stringify(selectedUsers.map((u)=> u._id)),
                },config);
                setChats([data,...chats]);
                onClose();
                toast({
                    title: "New Group Chat Created!",
                    status: "success",
                    duration: 5000,
                    isClosable: true,
                    position: "bottom",
                });
                
            } catch (error) {
                     toast({
                        title: "Failed to Create the Chat!",
                        description: error.response.data,
                        status: "error",
                        duration: 5000,
                        isClosable: true,
                        position: "bottom",
                    });
            }
         };

  return (
    <>
      <span onClick={onOpen}>{children}</span>

      <Modal onClose={onClose} isOpen={isOpen} isCentered>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader
            fontSize="35px"
            fontFamily="Work sans"
            display="flex"
            justifyContent="center"
          >
            Create Group Chat
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody display="flex" flexDirection="column" alignItems="center">
            <FormControl>
              <Input
                placeholder="Chat Name"
                marginBottom={3}
                onChange={(e) => setGroupChatName(e.target.value)}
              />
            </FormControl>
            <FormControl>
              <Input
                placeholder="Add Users eg: John, Piyush, Jane"
                marginBottom={1}
                onChange={(e) => handleSearch(e.target.value)}
              />
            </FormControl>
            <Box w="100%" display="flex" flexWrap="wrap">
              {selectedUsers.map((u) => (
                <UserBadgeItem
                  key={u._id}
                  user={u}
                  handleFunction={() => handleDelete(u)}
                />
              ))}
            </Box>
            {loading ? (
              <div>Loading...</div>
            ) : (
              searchResult?.slice(0, 4)
                .map((user) => (
                  <UserListItem
                    key={user._id}
                    user={user}
                    handleFunction={() => handleGroup(user)}
                  />
                ))
            )}
          </ModalBody>
          <ModalFooter>
            <Button onClick={handleSubmit} colorScheme="blue">
              Create Chat
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  )
}

export default GroupChatModal