import React from 'react'
import { AddIcon } from "@chakra-ui/icons";
import { Box, Stack, Text, Badge } from "@chakra-ui/layout";
import { useToast } from "@chakra-ui/toast";
import axios from "axios";
import { useEffect, useState } from "react";
import { Button } from "@chakra-ui/react";
import { ChatState } from "../Context/ChatProvider";
import ChatLoading from "../components/Miscellaneous/ChatlLoading";
import GroupChatModal from "../components/Miscellaneous/GroupChatModal";
import { getSender } from '../config/chatLogics';


const MyChats = ({fetchAgain}) => {
  const [loggedUser, setLoggedUser] = useState();
  const {selectedChat,setSelectedChat,user,chats,setChats,} = ChatState();
  const toast =  useToast();

  const fetchChats = async() => {
    try {
      const config = {
        headers : {
          Authorization: `Bearer ${user.token}`,
        }
      }
      const {data} = await axios.get("/api/v1/chat/",config);
      setChats(data);
       
    } catch (error) {
      toast({
        title: "Error Occured!",
        description: "Failed to Load the chats",
        status: "error",
        duration: 5000,
        isClosable: true,
        position: "bottom-left",
      });
    }
  }

  useEffect(()=>{
    setLoggedUser(JSON.parse(localStorage.getItem("userInfo")));
    fetchChats();
  },[fetchAgain]);
  
   return (
     <Box
      display={{ base: selectedChat ? "none" : "flex", md: "flex" }}
      flexDirection="column"
      alignItems="center"
      padding={3}
      bg="white"
      width={{ base: "100%", md: "31%" }}
      borderRadius="lg"
      borderWidth="1px"
    >
      <Box
        pb={3}
        px={3}
        fontSize={{ base: "28px", md: "30px" }}
        fontFamily="Work sans"
        display="flex"
        w="100%"
        justifyContent="space-between"
        alignItems="center"
      >
        My Chats
        <GroupChatModal>
          <Button
            display="flex"
            fontSize={{ base: "17px", md: "10px", lg: "17px" }}
            rightIcon={<AddIcon />}
          >
            New Group Chat
          </Button>
        </GroupChatModal>
      </Box>
      <Box
        display="flex"
        flexDir="column"
        p={3}
        bg="#F8F8F8"
        w="100%"
        h="100%"
        borderRadius="lg"
        overflowY="hidden"
      >
        {chats ? (
          <Stack overflowY="scroll">
            {chats.map((chat) => (
              <Box
                onClick={() => setSelectedChat(chat)}
                cursor="pointer"
                bg={selectedChat === chat ? "#38B2AC" : "#E8E8E8"}
                color={selectedChat === chat ? "white" : "black"}
                px={3}
                py={2}
                borderRadius="lg"
                key={chat._id}
              >
                <Text>
                  {!chat.isGroupChat
                    ? getSender(loggedUser, chat.users)
                    : chat.chatName}
                </Text>
                {chat.latestMessage && (
                  <Text fontSize="xs">
                     {/* <b>{chat.latestMessage.sender.name} : </b>
                    {chat.latestMessage.message.length > 50? chat.latestMessage.message.substring(0, 51) + "..."
                      : chat.latestMessage.message} */}
                  </Text>   
                )}
              </Box>
            ))}
          </Stack>
        ) : (
          <ChatLoading />
        )}
      </Box>
    </Box>
  );
}

export default MyChats